import { List } from '@material-ui/core';
import _get from 'lodash.get';
import { TOffer, TCounter } from '../../../../types';
import SubCard from '../../../../ui-component/cards/SubCard';
import TextItem from '../../components/TextItem';
import {
	displayFurniture,
	formatCurrency,
	getOfferPrice,
	displayRentBack,
	getLatestCounter,
} from '../../../../helpers';
import {
	displayLoanType,
	displayContingency,
	getProofOfFunds,
	CONTINGENCY_TYPES,
} from '../helper';
import HomeSaleInformation from './HomeSaleInformation';

type Props = {
	offer: TOffer;
};

const OfferInformation = ({ offer }: Props) => {
	const homeSale = _get(offer, 'HomeSale');
	const counterOffer = getLatestCounter(offer);
	return (
		<SubCard title='Offer Information'>
			<List>
				<TextItem<TOffer> data={offer} source='OfferNo' label='Offer No' />
				<TextItem<TOffer>
					data={offer}
					getValue={getOfferPrice}
					label='Offer Price'
				/>
				<TextItem<TOffer>
					data={offer}
					getValue={row =>
						formatCurrency(_get(row, 'Property.ListingDetails.ListPrice'))
					}
					label='Listing Price'
				/>
				<TextItem<TOffer>
					data={offer}
					source='Funding.PurchaseType'
					label='Funding Type'
				/>
				<TextItem<TOffer>
					data={offer}
					label='Proof of funds'
					getValue={getProofOfFunds}
				/>
				<TextItem<TOffer>
					data={offer}
					label='Loan Type'
					getValue={row => displayLoanType(row)}
				/>
				<TextItem<TOffer>
					data={offer}
					getValue={row => formatCurrency(_get(row, 'Funding.DownPayment'))}
					label='Down Payment'
					isHiddenEmptyValue
				/>
				<TextItem<TOffer>
					data={offer}
					source='Funding.FicoScore'
					label='FICO Score'
				/>
				<TextItem<TCounter>
					data={counterOffer}
					label='Finance Contingency'
					getValue={row =>
						displayContingency(row, CONTINGENCY_TYPES.financeContingency)
					}
				/>
				<TextItem<TCounter>
					data={counterOffer}
					label='Inspection Contingency'
					getValue={row =>
						displayContingency(row, CONTINGENCY_TYPES.inspectionContingency)
					}
				/>
				<TextItem<TCounter>
					data={counterOffer}
					label='Appraisal Contingency'
					getValue={row =>
						displayContingency(row, CONTINGENCY_TYPES.appraisalContingency)
					}
				/>
				<TextItem<TCounter>
					data={counterOffer}
					label='Home Sale Contingency'
					source={CONTINGENCY_TYPES.saleOfHomeContingency}
				/>
				<TextItem<TCounter>
					data={counterOffer}
					source='ClosingPeriod'
					label='Closing Period'
				/>
				<TextItem<TOffer>
					data={offer}
					getValue={displayFurniture}
					label='Personal Item Requests'
				/>
				<TextItem<TCounter>
					data={counterOffer}
					getValue={row => displayRentBack(row)}
					label='Rent Back'
				/>
				<TextItem<TOffer>
					data={offer}
					source='PropertyData.FullAddress'
					label='Property Address'
				/>
				<TextItem<TOffer>
					data={offer}
					source='PropertyData.OwnerNames'
					label='Sellers'
				/>
				<HomeSaleInformation homeSale={homeSale} />
			</List>
		</SubCard>
	);
};

export default OfferInformation;
