import _get from 'lodash.get';
import { TOfferHomeSale } from '../../../../../types';
import TextItem from '../../../components/TextItem';
import { formatDate } from '../../../../../helpers';
import { getListingAgentName, getOnMarketListingAgentName } from '../../helper';

type Props = {
	homeSale: TOfferHomeSale;
};

const OnMarket = ({ homeSale }: Props) => (
	<>
		<TextItem<TOfferHomeSale>
			data={homeSale}
			source='Property.Status'
			label='Home Sale Property Status'
		/>
		<TextItem<TOfferHomeSale>
			data={homeSale}
			getValue={row => formatDate(_get(row, 'OnMarket.OnMarketDate'))}
			label='Home Sale Property Date on Market'
		/>
		<TextItem<TOfferHomeSale>
			data={homeSale}
			source='OnMarket.HasOffers'
			label='Home Sale Property Offers'
			isHiddenEmptyValue
		/>
		<TextItem<TOfferHomeSale>
			data={homeSale}
			source='OnMarket.BrokerageFirm'
			label='Home Sale Brokerage Firm'
		/>
		<TextItem<TOfferHomeSale>
			data={homeSale}
			getValue={row => getListingAgentName(getOnMarketListingAgentName(row))}
			label='Home Sale Listing Agent Name'
		/>
	</>
);

export default OnMarket;
