import noUiSlider from 'nouislider';
import wNumb from 'wnumb';
import _get from 'lodash.get';
import _forEach from 'lodash.foreach';
import config from '../../../../../../config';

const tooltipFormatter = wNumb({
	prefix: `${config.locate.currency} `,
	decimals: config.locate.currencyDecimal,
	thousand: config.locate.currencyThousand,
});

const metaData = [
	{
		name: 'step:number',
		category: 'slider',
		categoryIndex: 1,
		default: 1,
	},
	{
		name: 'rangeMin:number',
		category: 'slider',
		default: 0,
	},
	{
		name: 'rangeMax:number',
		category: 'slider',
		default: 100,
	},
	{
		name: 'pipsMode',
		category: 'slider',
		default: 'positions',
	},
	{
		name: 'pipsValues:itemvalues',
		category: 'slider',
		default: [0, 25, 50, 75, 100],
	},
	{
		name: 'pipsText:itemvalues',
		category: 'slider',
		default: [0, 25, 50, 75, 100],
	},
	{
		name: 'pipsDensity:number',
		category: 'slider',
		default: 5,
	},
	{
		name: 'orientation',
		category: 'slider',
		default: 'horizontal',
		choices: ['horizontal', 'vertical'],
	},
	{
		name: 'direction:string',
		category: 'slider',
		default: 'ltr',
	},
	{
		name: 'tooltips:boolean',
		category: 'slider',
		default: true,
	},
];

const NoUiSliderInput = survey => {
	const widget = {
		name: 'nouislider',
		title: 'noUiSlider',
		iconName: 'icon-nouislider',
		widgetIsLoaded() {
			return typeof noUiSlider !== 'undefined';
		},
		isFit(question) {
			return question.getType() === 'nouislider';
		},
		htmlTemplate: '<div><div></div></div>',
		activatedByChanged() {
			survey.JsonObject.metaData.addClass('nouislider', [], null, 'empty');
			survey.JsonObject.metaData.addProperties('nouislider', metaData);
		},
		afterRender(question, el) {
			// eslint-disable-next-line no-param-reassign
			el.style.paddingBottom = '19px';
			// eslint-disable-next-line no-param-reassign
			el.style.paddingLeft = '20px';
			// eslint-disable-next-line no-param-reassign
			el.style.paddingRight = '20px';
			// eslint-disable-next-line no-param-reassign
			el.style.paddingTop = '44px';
			// eslint-disable-next-line no-param-reassign,prefer-destructuring
			el = el.children[0];
			// eslint-disable-next-line no-param-reassign
			el.style.marginBottom = '60px';
			if (question.orientation === 'vertical') {
				// eslint-disable-next-line no-param-reassign
				el.style.height = '250px';
			}
			const tooltip = question.tooltips ? tooltipFormatter : false;
			const slider = noUiSlider.create(el, {
				start: question.value || (question.rangeMin + question.rangeMax) / 2,
				connect: [true, false],
				step: question.step,
				tooltips: [tooltip],
				pips: {
					mode: _get(question, 'pipsMode', false) || 'positions',
					values: question.pipsValues.map(pVal => {
						let pipValue = pVal;
						if (pVal.value !== undefined) {
							pipValue = pVal.value;
						}
						// eslint-disable-next-line radix
						return parseInt(pipValue);
					}),
					density: _get(question, 'pipsDensity') || 5,
					format: {
						to(pVal) {
							let pipText = pVal;
							const pipsText = _get(question, 'pipsText', []);
							_forEach(pipsText, el => {
								if (_get(el, 'text') !== undefined && pVal === _get(el, 'value')) {
									pipText = _get(el, 'text');
								}
							});
							return pipText;
						},
					},
				},
				range: {
					min: _get(question, 'rangeMin'),
					max: _get(question, 'rangeMax'),
				},
				orientation: _get(question, 'orientation'),
				direction: _get(question, 'direction'),
			});
			slider.on('change', () => {
				// eslint-disable-next-line no-param-reassign
				question.value = Number(slider.get());
			});
			// eslint-disable-next-line func-names
			const updateValueHandler = function () {
				slider.set(_get(question, 'value'));
				slider.updateOptions({
					range: {
						min: _get(question, 'rangeMin'),
						max: _get(question, 'rangeMax'),
					},
				});
			};
			if (question.isReadOnly) {
				el.setAttribute('disabled', true);
			}
			updateValueHandler();
			// eslint-disable-next-line no-param-reassign
			question.noUiSlider = slider;
			// eslint-disable-next-line no-param-reassign
			question.valueChangedCallback = updateValueHandler;
			// eslint-disable-next-line no-param-reassign,func-names
			question.readOnlyChangedCallback = function () {
				if (question.isReadOnly) {
					el.setAttribute('disabled', true);
				} else {
					el.removeAttribute('disabled');
				}
			};
		},
		willUnmount(question) {
			const noUiSlider = _get(question, 'noUiSlider', false);
			if (noUiSlider) {
				question.noUiSlider.destroy();
				// eslint-disable-next-line no-param-reassign
				question.noUiSlider = null;
			}
			// eslint-disable-next-line no-param-reassign
			question.readOnlyChangedCallback = null;
		},
		pdfRender(_, options) {
			if (options.question.getType() === 'nouislider') {
				const point = options.module.SurveyHelper.createPoint(
					options.module.SurveyHelper.mergeRects.apply(null, options.bricks)
				);
				point.xLeft += options.controller.unitWidth;
				point.yTop +=
					options.controller.unitHeight *
					options.module.FlatQuestion.CONTENT_GAP_VERT_SCALE;
				const rect = options.module.SurveyHelper.createTextFieldRect(
					point,
					options.controller
				);
				const textboxBrick = new options.module.TextFieldBrick(
					options.question,
					options.controller,
					rect,
					true,
					options.question.id,
					options.question.value || options.question.defaultValue || '',
					'',
					options.question.isReadOnly,
					false,
					'text'
				);
				options.bricks.push(textboxBrick);
			}
		},
	};
	survey.CustomWidgetCollection.Instance.addCustomWidget(widget, 'customtype');
};

export { NoUiSliderInput };
