import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { TOfferHomeSale } from '../../../../types';
import HomeSaleEmpty from './HomeSales/Empty';
import HomeSaleOnMarket from './HomeSales/OnMarket';
import HomeSaleOther from './HomeSales/Other';
import { isOnMarket } from '../helper';

type Props = {
	homeSale: TOfferHomeSale;
};

const HomeSaleInformation = ({ homeSale }: Props) => {
	const propertyStatus = _get(homeSale, ['Property', 'Status']) || '';
	if (_isEmpty(propertyStatus)) return <HomeSaleEmpty homeSale={homeSale} />;

	if (isOnMarket(propertyStatus))
		return <HomeSaleOnMarket homeSale={homeSale} />;

	return <HomeSaleOther homeSale={homeSale} />;
};

export default HomeSaleInformation;
